* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.gallery__section {
  background-image: linear-gradient(#fff5f6 20%, #fff5f6 20%, #fff 15%);
}

.gallery__list {
  justify-content: center;
  align-items: center;
  display: flex;
}

.gallery__list-item:not(:last-child) {
  margin-right: 4px;
}

.gallery__list-item-two {
  margin-top: 9px;
}

.gallery__list-item-four {
  margin-top: 15px;
}

.gallery__list-item-six {
  margin-top: 9px;
}

.gallery__image-two, .gallery__image-four, .gallery__image-six, .gallery__image-eight {
  margin-bottom: 4px;
}

@media screen and (min-width: 480px) {
  .gallery__list {
    justify-content: space-between;
  }
}

